  created () {},
  mounted () {
    // 实时更新用户名
    window.addEventListener('setItem', () => {
      this.username = JSON.parse(window.sessionStorage.user).username || JSON.parse(window.sessionStorage.user).adminname
    })
  },
  methods: {
    totop () {
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      })
    }
  }
}
</script>

